import React,{useEffect, useState} from 'react';
import './App.css';
import Recipe from './Recipe';


const App = () => {

  const APP_ID = "520831b8";
  const APP_KEY = "bf77dd8c6970d0ad3aa9bc2488934842";
  //const TESTGETREQUESTLINK ="https://api.edamam.com/search?q=chicken&app_id=${YOUR_APP_ID}&app_key=${YOUR_APP_KEY}&from=0&to=3&calories=591-722&health=alcohol-free";

  const exampleReq = `https://api.edamam.com/search?q=chicken&app_id=${APP_ID}&app_key=${APP_KEY}`;

  //const [counter, setCounter] = useState(0);

  const[recipes, setRecipes] = useState([]); 
  const [search, setSearch] = useState("");
  const[query, setQuery] = useState('chicken');


  useEffect( () => {
    //console.log('Effect has been run');
    getRecipes();
  },[query]);

  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    const data = await response.json();
    console.log(data);
    setRecipes(data.hits);
    console.log(data.hits);
  }

  const updateSearch = e => {
    setSearch(e.target.value);
    console.log(search);
  }

  const getSearch = e => {
    e.preventDefault();
    console.log();
    setQuery(search)
  }  

  return(
    <div className="App">
      <div className="headerwrapper">
        <h1>Deng's Recipes</h1>
      </div>
      <form onSubmit={getSearch} className="search-form">
        <input className="search-bar" type="text" value={search} onChange={updateSearch}></input>
        <button className="search-button" type="submit">Search</button>
      </form>
      <div className="recipes">
      {recipes.map(recipe =>(
        <Recipe key={recipe.recipe.label} title={recipe.recipe.label} calories={recipe.recipe.calories} image={recipe.recipe.image} ingredients={recipe.recipe.ingredients}></Recipe>
      ))}
      </div>
    </div>
  );
}; 

export default App;
